$menuWidth: 320px;
$navWidth: 44px;
$navHeight: $navWidth;
$navInfoWidth: 250px;

$primeHeight: 70px;

$notifyHeight: 30px;

html, body, .body2, .BUNS, .BUNS-contentScroller {
	width: 100%;
	height: 100%;

	overflow: hidden;

	font-family: "Source Sans Pro", sans-serif;
	font-weight: 300;
}
.wrapper {
	position: relative;
	width: 100%;
	height: 100%;

	&-scrollable {
		position: relative;
		width: 100%;
		height: 100%;

		overflow: auto;
	}
}
.BUNS {
	position: relative;
	z-index: 0;
	padding-top: $primeHeight;
	padding-left: 0;
	padding-right: 0;
	background: #DDD;
	transition: padding .2s ease-in-out;


	&.BUNS--youOpen {
		padding-left: $menuWidth;
	}
	&.BUNS--meOpen {
		padding-right: $menuWidth;
	}
	&.BUNS--notifyOpen {
		padding-bottom: $notifyHeight;
	}
}
.BUNS-prime {
	position: absolute;
	left: 0;
	top: 0;
	z-index: 4;
	width: 100%;
	height: $primeHeight;
	background: #AAA;
}
.BUNS-notify {
	position: absolute;
	left: 0;
	bottom: -$notifyHeight;
	z-index: 3;
	width: 100%;
	height: $notifyHeight;
	background: #000;
	transition: all .2s ease-in-out;

	.BUNS--notifyOpen & {
		bottom: 0;
	}
}

.BUNS-sideNav {
	position: absolute;
	top: 0;
	z-index: 3;
	width: $navWidth;
	height: 100%;
	background: #333;
	transition: all .2s ease-in-out;
}
.BUNS-sideNavInfo {
	position: absolute;
	top: 0;
	z-index: 2;

	width: $navWidth + $navInfoWidth;
	height: 100%;

	background: #666;
	background: rgba( 0, 0, 0, 0.6 );

	transition: all .2s ease-in-out;
}
.BUNS-you,
.BUNS-me {
	background: #CCC;
}



.BUNS-you {
	position: absolute;
	left: -$menuWidth;
	top: 0;
	z-index: 1;
	width: $menuWidth;
	height: 100%;
	padding-top: $primeHeight;
	padding-left: $navWidth;
	transition: all .2s ease-in-out;

	.BUNS--notifyOpen & {
		padding-bottom: $notifyHeight;
	}

	.BUNS--youOpen & {
		left: 0px;
		z-index: 2;
	}


	&__nav {
		right: 0;

		.BUNS--youOpen & {
			right: $menuWidth - $navWidth;
		}
	}

	&__navInfo {
		right: 0;
	}
}


.BUNS-you__nav--over + .BUNS-you__navInfo {
	right: -$navInfoWidth;
	transition-delay: .1s;
}
.BUNS--youOpen .BUNS-you__navInfo {
	right: $menuWidth - $navWidth;
}
.BUNS--youOpen .BUNS-you__nav--over + .BUNS-you__navInfo {
	right: $menuWidth - $navInfoWidth - $navWidth;
	transition-delay: .1s;
}



.BUNS-me {
	position: absolute;
	right: -$menuWidth;
	top: 0;
	z-index: 1;
	width: $menuWidth;
	height: 100%;
	padding-top: $primeHeight;
	padding-right: $navWidth;
	transition: all .2s ease-in-out;
}
.BUNS--notifyOpen .BUNS-me {
	padding-bottom: $notifyHeight;
}
.BUNS--meOpen .BUNS-me {
	right: 0px;
	z-index: 2;
}
.BUNS-me__nav {
	left: 0;
}
.BUNS-me--over .BUNS-sideNav {
	left: $navWidth + $navInfoWidth;
}
.BUNS--meOpen .BUNS-me__nav {
	left: $menuWidth - $navWidth;
}
.BUNS-me__navInfo {
	left: 0;
}
.BUNS-me__nav--over + .BUNS-me__navInfo {
	left: -$navInfoWidth;
	transition-delay: .1s;
}
.BUNS--meOpen .BUNS-me__navInfo {
	left: $menuWidth - $navWidth;
}
.BUNS--meOpen .BUNS-me__nav--over + .BUNS-me__navInfo {
	left: $menuWidth - $navInfoWidth - $navWidth;
	transition-delay: .1s;
}


.BUNS-navToggle {
	position: absolute;
	top: 0;
	width: $navWidth;
	height: $primeHeight;
	background: #333 50% 50% no-repeat;
	transition: all .2s ease-in-out;
}
.BUNS-prime {
	&__youToggle {
		left: 0;
		background-image: url( "../../i/icons44_BUNSYou.svg" );
	}
	&__meToggle {
		right: 0;
		background-image: url( "../../i/icons44_BUNSMe.svg" );
	}
}
.BUNS-content {
	min-width: $menuWidth;
}
.box {
	float: left;
	width: 90%;
	padding-bottom: 60%;
	margin: 20px 5%;
	background: #BBB;
	transition: all .2s ease-in-out;
}
.BUNS-navItem {
	width: $navWidth;
	height: $navHeight;

	transition: all .4s ease-in-out;
	transition-delay: .2s;

	.BUNS-you__nav--over & {
		border-radius: 8px;
		transition: all .2s ease-in-out;
		transition-delay: 0;
	}

	&--active {
		background: #CCC;
	}

	&__toggle {
		width: 100%;
		height: 100%;

		background: transparent 50% 50% no-repeat;
		opacity: 0.6;

		transition: all .2s ease-in-out;

		&--over {
			background-color: rgba( 255, 255, 255, 0.4 );
		}
		&--down {
			background-color: rgba( 0, 0, 0, 0.4 );
		}
		&--overdown {
			background-color: rgba( 0, 0, 0, 1 );
			transition: all .02s;
		}

		.BUNS-navItem--active & {
			opacity: 1;
			background-color: transparent;
			cursor: default;
		}

		.BUNS-navItem-nav & {
			background-image: url( "../../i/icons44_BUNSYou__nav.svg" );
		}

		.BUNS-navItem-games & {
			background-image: url( "../../i/icons44_BUNSYou__games.svg" );
		}

		.BUNS-navItem-favourites & {
			background-image: url( "../../i/icons44_BUNSYou__favourites.svg" );
		}
	}
}

.BUNS-module {
	padding: 20px;

	&__title {
		padding: .3em 0;
		line-height: 1.3;
		margin: 0;

		background: gray;

		color: white;
		font-size: 2em;
		text-align: center;
	}
}

@media all and (min-width: 680px) {
	.BUNS {
		padding-left: $navWidth;
		padding-right: $navWidth;
	}
	.BUNS-prime__youToggle {
		left: -$navWidth;
	}
	.BUNS-prime__meToggle {
		right: -$navWidth;
	}
	.BUNS-you {
		left: -($menuWidth - $navWidth);
	}
	.BUNS-me {
		right: -($menuWidth - $navWidth);
	}
}
@media all and (min-width: 800px) {
	.box {
		width: 45%;
		padding-bottom: 30%;
		margin: 20px 2.5%;
	}
}
@media all and (min-width: 1100px) {
	.box {
		width: 29.3333%;
		padding-bottom: 20%;
		margin: 20px 2%;
	}
}
@media all and (min-width: 1400px) {
	.box {
		width: 22%;
		padding-bottom: 16%;
		margin: 20px 1.5%;
	}
}